import { logger } from '../logger';
import { ERROR_MESSAGES } from './constants';

/**
 * 
 */
export function formatError(error: unknown): string {
  if (error instanceof Error) {
    return error.message;
  }
  return String(error);
}

/**
 * 
 */
export function getErrorMessage(error: unknown): string {
  const errorMessage = formatError(error);

  // 
  if (
    errorMessage.includes('Attempt to debit an account but found no record of a prior credit') ||
    errorMessage.includes('insufficient funds')
  ) {
    return ERROR_MESSAGES.INSUFFICIENT_FUNDS;
  }

  if (errorMessage.includes('not connected')) {
    return ERROR_MESSAGES.WALLET_NOT_CONNECTED;
  }

  // 
  return errorMessage;
}

/**
 * 
 * 
 */
export function withErrorHandling<T extends (...args: any[]) => Promise<any>>(
  fn: T,
  options: {
    showLoading?: (message: string) => void;
    hideLoading?: () => void;
    showError?: (message: string) => void;
    logPrefix?: string;
  } = {}
): T {
  return (async (...args: Parameters<T>): Promise<ReturnType<T>> => {
    try {
      return await fn(...args);
    } catch (error) {
      const errorMessage = getErrorMessage(error);

      // 
      if (options.logPrefix) {
        logger.error(`${options.logPrefix}: ${errorMessage}`);
      } else {
        logger.error(errorMessage);
      }

      // UI 
      if (options.showError) {
        options.showError(errorMessage);
      }

      // 
      throw error;
    } finally {
      // 
      if (options.hideLoading) {
        options.hideLoading();
      }
    }
  }) as T;
}

/**
 * 
 */
export async function requireWalletConnection<T>(
  walletState: { connected: boolean },
  operation: () => Promise<T>,
  onError?: (message: string) => void
): Promise<T> {
  if (!walletState.connected) {
    const message = ERROR_MESSAGES.WALLET_NOT_CONNECTED;
    logger.error(message);
    if (onError) {
      onError(message);
    }
    throw new Error(message);
  }

  return await operation();
}

/**
 * 
 */
export async function executeTransaction<T>(
  operation: () => Promise<T>,
  options: {
    operationName: string;
    showLoading: (message: string) => void;
    hideLoading: () => void;
    showSuccess: (message: string) => void;
    showError: (message: string) => void;
    successMessage: string;
    onSuccess?: () => Promise<void>;
  }
): Promise<T> {
  try {
    options.showLoading(`${options.operationName}...`);

    const result = await operation();

    // 
    if (options.onSuccess) {
      await options.onSuccess();
    }

    options.showSuccess(options.successMessage);
    return result;
  } catch (error) {
    const errorMessage = getErrorMessage(error);
    logger.error(`${options.operationName}: ${errorMessage}`);
    options.showError(`${options.operationName}: ${errorMessage}`);
    throw error;
  } finally {
    options.hideLoading();
  }
}

/**
 * 
 * (: 'already_initialized', 'already_owned' )
 */
export async function executeTransactionWithSpecialReturns<T>(
  operation: () => Promise<T>,
  options: {
    operationName: string;
    showLoading: (message: string) => void;
    hideLoading: () => void;
    showSuccess: (message: string) => void;
    showError: (message: string) => void;
    successMessage: string;
    specialReturns?: Record<string, string>; //  -> 
    onSuccess?: () => Promise<void>;
  }
): Promise<T> {
  try {
    options.showLoading(`${options.operationName}...`);

    const result = await operation();

    // 
    if (options.specialReturns && typeof result === 'string' && result in options.specialReturns) {
      options.showSuccess(options.specialReturns[result]);
    } else {
      options.showSuccess(options.successMessage);
    }

    // 
    if (options.onSuccess) {
      await options.onSuccess();
    }

    return result;
  } catch (error) {
    const errorMessage = getErrorMessage(error);
    logger.error(`${options.operationName}: ${errorMessage}`);
    options.showError(`${options.operationName}: ${errorMessage}`);
    throw error;
  } finally {
    options.hideLoading();
  }
}
